#dropdown-toggle {
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  border: 1px solid #0000;
  padding: .375rem .75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  display: inline-block;
}

#dropdown-toggle:after {
  vertical-align: .255em;
  content: "";
  border: .3em solid #0000;
  border-top-color: currentColor;
  border-bottom: 0;
  display: inline-block;
}

:host {
  display: inline-block;
  position: relative;
}

input.text {
  width: 100%;
}

.input-group {
  flex-direction: var(--direction, row);
  margin: var(--margin);
  background-color: #e9ecef;
  border: 1px solid #ced4da;
  border-radius: .25rem;
  flex-wrap: wrap;
  padding: .375rem;
  display: inline-flex;
}

.input-group div {
  display: inline-flex;
}

.input-group.labels-vertical label {
  flex-basis: 100%;
}

.rounded {
  border-radius: var(--border-radius, .25rem);
}

.right-rounded {
  border-top-right-radius: var(--border-top-right-radius, .25rem);
  border-bottom-right-radius: var(--border-bottom-right-radius, .25rem);
}

.left-rounded {
  border-top-left-radius: var(--border-top-left-radius, .25rem);
  border-bottom-left-radius: var(--border-bottom-left-radius, .25rem);
}

.no-rounded {
  border-radius: 0;
}

slot[name="dropdown"] {
  z-index: 1000;
  color: #212529;
  text-align: left;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #00000026;
  border-radius: .25rem;
  width: 100%;
  margin: 0;
  font-size: 1rem;
  display: none;
  position: absolute;
}

::slotted(ul) {
  flex-direction: column;
  margin: 0;
  padding: .5rem 1rem;
  list-style: none;
  display: flex;
}

::slotted(ol) {
  flex-direction: column;
  margin: 0;
  padding: .5rem 0;
  list-style: none;
  display: flex;
}
